@import "mixins";
@import "variebles";
@import "header";
@import "cards";

.moyo_catalog {
  visibility: hidden;
  position: absolute;
}

p {
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 11px;
  line-height: 13px;
}

.main {
  display: grid;
  grid-template-columns: repeat(4, $width-card);
  justify-content: center;
  grid-gap: 50px 48px;
}
